@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
/*this page is responsable for the styling of the whole project*/

:root {
  --dark-bg-color:hsl(207,26%,17%);
  --article-color:hsl(209, 23%, 22%);
  --text-color:hsl(0, 0%, 100%);
  --light-mode-text:hsl(200, 15%, 8%);
  --light-mode-input:hsl(0, 0%, 52%);
  --light-mode-background:hsl(0, 0%, 98%);
}



*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

ul {
  list-style-type: none;
}

.buttons {
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.btn {
  background-color: hsl(207,26%,17%);
  background-color: var(--dark-bg-color);
  padding: 5px;
  cursor:pointer;
  text-decoration: none;
  color: #fff;
  border: 2px solid hsl(207,26%,17%);
  border: 2px solid var(--dark-bg-color);
  border-radius: 4px;
  transition: background-color 0.2s;
}

.btn:hover {
  background-color: #fff;
  color: hsl(207,26%,17%);
  color: var(--dark-bg-color);
}

.btn-light {
  background-color: hsl(209, 23%, 22%);
  background-color: var(--article-color);
  border: 2px solid hsl(209, 23%, 22%);
  border: 2px solid var(--article-color);
}

body {
  font-family: 'Nunito Sans' , sans-serif;
  /*padding: 20px;*/
  background-color: #202D36;
  color: #fff;
}

body.light-theme {
  background-color:hsl(0, 0%, 98%);
  background-color:var(--light-mode-background);
  color:hsl(200, 15%, 8%);
  color:var(--light-mode-text);
}

/*Header*/
.header {
display: -webkit-flex;
display: flex;
-webkit-align-items: center;
        align-items: center;
-webkit-justify-content: space-between;
        justify-content: space-between;
background-color: #2b3743;
padding: 10px 20px;
}

header .light-theme {

}

/*Filter*/
.filter {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;

}

.filter .form-control {
  width: 100%;
}

.filter .form-control input {
  background-color: #2b3743;
  border: none;
  outline: none;
  padding:  12px;
  border-radius: 5px;
  color: #fff;
  font-family: inherit;
  letter-spacing: 0.5px;
  width: 100%;
  margin: 0 0 30px;
}

.filter .form-control input::-webkit-input-placeholder {
  color: #fff;
  background-color: #2b3743;
}

.filter .form-control input:-ms-input-placeholder {
  color: #fff;
  background-color: #2b3743;
}

.filter .form-control input::placeholder {
  color: #fff;
  background-color: #2b3743;
}


.filter .region-filter {
  /*width: 100%;*/
  background-color: #2b3743;
}

.filter .region-filter  .select{
background-color: #2b3743;
color: #fff;
font-family: inherit;
padding: 8px;
width: 200px;
border: none;
border-radius: 5px;
}

/*Grid*/
.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 40px;
  gap: 40px;
  padding: 20px;
}

.grid article{
 /* box-shadow: 0 10px rgba (9, 17, 22, 0.5); */
}

.grid article img {
border-radius: 5px 5px 0 0;
/*height: 250px;*/
}

.grid .details {
  background-color: #2b3743;
  padding: 40px 20px;
  margin-top: -20px;
  border-radius: 0 0 5px 5px;
}

.grid .details h3 {
  padding-bottom: 10px;
}

.grid .details h4 {
  padding-bottom: 5px;
}

.grid .details h4 span {
  font-weight: 300;
}

@media (min-width: 640px){
  .grid{
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px){

  .filter {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }

  .filter .form-control {
    width: auto;
  }

  .filter .form-control input {
    width: auto;
    margin: 0;
  }

  .filter .region-filter {
    width: auto;
  }

  .grid{
    grid-template-columns: repeat(3, 1fr);
  }
}


@media (min-width: 992px){
 .header .filter .grid {
   padding: 20px 100px;
 }

  .grid{
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 60px;
    gap: 60px;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*this page is responsable for the styling of the country individual page*/
.country {
    padding: 50px;

}

.btn-light {
    margin-bottom: 30px;
    display: inline-block;
}

.country article .country-details div {
    font-size: 30px;
    letter-spacing: 1px;
}

.country article .country-details div h2 {
    font-weight: 600;
    font-size: 18px;
    margin: 10px 0;
}

.country article .country-details div h5 {
    font-weight: 600;
    font-size: 18px;
    margin: 10px 0;

}

.country article .country-details div h5  span{
    font-weight: 300;
}

.coutry article .country-details h3 {
    font-weight: 600;
}

.country article .borders {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.country article .borders ul {
    margin: 10px;
    background-color: var(--article-color);
    padding: 3px 15px;
    border-radius: 3px;
    box-shadow: 0 5px 20px hsla(200, 15%, 8%, 0.5);
}

@media (min-width: 992px) {
.country article .country-inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr); 
    grid-gap: 20px; 
    gap: 20px;  
}

.country article .country-details {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
}

@media (min-width: 1200px) {
    .country{
        max-width: 1100px;
        margin: auto;
    }    
}
